.activator {
    
  height: auto;
  max-height: 250px;
  width: auto;
  max-width: 250px;
}

.starbucks-info {
  display: flex !important;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.bottom-container {
  display: flex;
  justify-content: space-between;
}

.starbucks-name {
  font-weight: 700;
}

.details {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 12px;
  color: #ced1dd;
}

.close-container {
  display: inline-block;
  width: 100%;
}

.reveal-details {
  margin-top: 20px;
}

.reveal-details > div > span {
  color: #ced1dd;
  text-transform: uppercase;
  margin: 5px 0 5px 0;
}

.detail-item {
  display: flex;
  justify-content: space-between;
}
